.fb-feature-list-wrapper {
    display: flex;
    align-items: center;
    transition: ease-in-out .5s;

    &.fb-feature-list-left {
        justify-content: flex-start;
    }
    &.fb-feature-list-center {
        justify-content: center;
    }
    &.fb-feature-list-right {
        justify-content: flex-end;
    }
    &.fb-icon-position-left {
        .fb-feature-list-item {
            display: flex;
            align-items: center;
            text-align: left;
        }
    }
    &.fb-icon-position-right {
        .fb-feature-list-item {
            display: flex;
            align-items: center;
            flex-direction: row-reverse;
            text-align: right;

            .fb-feature-list-icon-inner {
                margin-left: 20px;
            }
            .fb-feature-icon-image-wrapper {
                margin-left: 20px;
            }
        }
    }

    .fb-feature-list-items {
        width: 100%;
        list-style-type: none !important;
        margin: 0 !important;
        padding: 0 !important;
        text-align: center;
    }
}

.fb-feature-list-item {
    position: relative;
    align-items: center;

    .fb-feature-list-icon-box {
        transition: ease-in-out .5s;
        z-index: 2;

        .fb-feature-list-icon-inner {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            transition: ease-in-out .5s;

            .fb-feature-list-icon {
                align-items: center;
                display: flex;
                justify-content: center;
                transition: all 0.5s;

                svg {
                    transition: all 0.5s;
                }
            }

            &.circle {
                border-radius: 50%;
            }
            &.square {
                border-radius: 0%;
            }
            &.rhombus {
                margin: 15px 0;
                transform: rotate(45deg);
                .fb-feature-list-icon {
                    transform: rotate(-45deg);
                }
            }
            &.none {
                display: none !important;
            }

        }
        .fb-feature-icon-image-wrapper {
            display: inline-flex;
            transition: ease-in-out .5s;
            img {
                width: 100%;
                height: 100%;
                transition: ease-in-out .5s;
            }
            &.circle {
                img {
                    border-radius: 50%;
                }
            }
            &.square {
                img{
                    border-radius: 0%;
                }
            }
            &.rhombus {
                img {
                    margin: 15px 0;
                    transform: rotate(45deg);
                }
            }
            &.none {
                display: none !important;
            }
        }
    }

    .fb-feature-list-content {
        .fb-feature-list-title {
            margin: 0 0 5px;
            padding: 0;
        }

        .fb-feature-list-text {
            margin: 0;
            padding: 0;
        }
    }
}

@media (min-width: 1025px) {
    .fb-icon-position-left {
        .fb-feature-list-item {
            flex-direction: row;
            text-align: left;

            &,
            .fb-icon-position-right .fb-feature-list-item {
                align-items: flex-start;
                display: flex;
            }
        }

        .fb-feature-list-content-box {
            margin: 0 !important;
        }
    }
}
